.favorites-list {
  @extend %list;
}

.favorites-item {
  margin-bottom: 30px;
}

.favorites-image {
  padding: 24px;
  background-color: #fefefe;
  border: 16px solid #ffe550;

  border-radius: 50%;
}

.favorites-image:hover,
.favorites-image:focus {
  border: 16px solid #bbf330;
}

.favorites-image:active {
  border: 16px solid blue;
}
